  overflow: hidden;
  cursor: pointer;
}
#button-2 a {
  position: relative;
  -webkit-transition: all .35s ease-Out;
  transition: all .35s ease-Out;
}
#slide {
  width: 100%;
  height: 100%;
  left: -200px;
  background: white;
  position: absolute;
  -webkit-transition: all .35s ease-Out;
  transition: all .35s ease-Out;
  bottom: 0;
}
#button-2:hover #slide {
  left: 0;
}
#button-2:hover a {
  color: black;
}
